<template>
  <div class="merchan">
      <!-- 我是商品页面 -->
      <!-- 左侧菜单导航栏 -->
      <div class="menu-wrap" ref="menuWrapper">
      	<ul class="menu-ul">
      	<!-- :class="'current'=currentIndex===index" -->       
      		<li v-for="(item,index) in goods" class="menu-li" @click="selectMenu(index, $event)" :class="{'current':currentIndex === index}">
      			<span class="text">{{item.name}}</span>
      		</li>
      	</ul>
      </div>
      <!-- 右侧内容栏 -->
      <div class="foodShow" ref="foodWrapper">
      	<ul>
      		<li v-for="item in goods" class="food-list food-list-hook">
      			<h1 class="title">{{item.name}}</h1>
      			<ul>
      				<li v-for="food in item.foods" class="food-item" @click.stop.prevent="foodDetail(food)">
      					<div class="icon">
      						<img :src="food.icon" width="57">
      					</div>
      					<div class="content">
      						<h2 class="name">{{food.name}}</h2>
      						<p class="des">{{food.description}}</p>
      						<div class="extra">
      							<span class="count">月售{{food.sellCount}}</span>
      							<span class="count">好评{{food.rating}}</span>
      						</div>
      						<div class="price">
      							<span class="now">￥{{food.price}}</span>
      							<span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
      						</div>
                  <div class="cartControl-wrapper">
                      <!-- 商品的增减以及数量的变化 -->
                      <!-- <cartcontrol :food="food" @increment="incrementTotal"></cartcontrol> -->
                      <cartcontrol :food="food" @increment="incrementTotal"></cartcontrol>
                  </div>
      					</div>
      				</li>
      			</ul>
      		</li>
      	</ul>
      </div>
      <!-- 注意：:select-foods="selectFoods" 前后驼峰形式：首单词是后面传入单词的横杆形式 -->
      <shopCart :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price = "seller.minPrice" ref="shopCart"></shopCart>
      <food :foodselected="selectedFood" ref="food"></food>
  </div>
</template>

<script>
import BScroll from "better-scroll";
import shopCart from "./shopCart";
import cartcontrol from "../cartControl/cartControl.vue"
import {api} from "../../global/api.js"
import food from "../foodDetail/foodDetail"
export default {	
    data (){
      return {
      	seller:{},
      	goods:[],
      	scrolly:0,
      	listHeight:[],
        selectedFood:{}
      }
    },
    computed:{
    	currentIndex(){
        for(var i =0;i<this.listHeight.length;i++){
          let height1 = this.listHeight[i];
          let height2 = this.listHeight[i+1];
          // console.log("height1:",height1);
          // console.log("height1",height2);
          if(!height2||(this.scrolly>=height1&&this.scrolly<height2)){
            return i
          }
        }
        return 0;
      },
      selectFoods(){
        let foods = [];
        this.goods.forEach((item)=>{
          item.foods.forEach((food)=>{
            if(food.count){
              foods.push(food);
            }
          })
        })
        return foods;
      }
    },
    methods:{
    	_initScroll() {
        this.menuScroll = new BScroll(this.$refs.menuWrapper,{
          click:true
        })
        this.foodScroll = new BScroll(this.$refs.foodWrapper,{
          probeType:3,//如果没有设置该值，其默认值为 0，即不派发 scroll 事件
          click:true
        })
        // 获取右侧滚动的Y轴位置
        this.foodScroll.on('scroll',(pos)=>{
          this.scrolly = Math.abs(Math.round(pos.y));
        })
      },

      _calculateHeight() {
        let foodList = this.$refs.foodWrapper.getElementsByClassName('food-list-hook');
        // let foodList = document.getElementsByClassName('food-list-hook');
        let iHeight = 0;
        this.listHeight.push(iHeight);
        let item = 0;
        for(var i = 0;i<foodList.length;i++){
          item = foodList[i].clientHeight;
          iHeight += item;
          this.listHeight.push(iHeight);
        }
      },

      selectMenu(index,event) {
        if(!event._constructed){
          // 去掉自带click事件的点击
          return;
        }
        console.log("this.currentIndex:",this.currentIndex)
        console.log("index:",index)
        let foodList = document.getElementsByClassName('food-list-hook');
        let el = foodList[index];
        this.foodScroll.scrollToElement(el,300);
      },
      
      incrementTotal(target) {
        this.$refs.shopCart.drop(target);
      },
      foodDetail(food){
        this.selectedFood = food;
        this.$refs.food.show();//调用子组件的函数
      }
    },
    created(){
        let that = this;
           // this.$http.get('../static/data/data.json').then(response => {
            this.$http.get(api.merchan).then(response => {
                console.log("这是我们需要的json数据",response.data);
               // 注意获取的是数组还是对象，我这里获取的是对象
                that.seller = response.data.seller;
                that.goods = response.data.goods;
                console.log("that.seller:",that.seller);
                console.log("that.goods:",that.goods);
            }, response => {
                console.log("请求失败");
            });
            //随时更新的函数入口
            // this.$nextTick(()=>{//用这个代码竟然不能实时更新currentIndex,每次都是0
            //    that._initScroll();
            //    that._calculateHeight();
            // })
            setTimeout(()=>{
                that._calculateHeight();
            },10)
            this.$nextTick(()=>{
                that._initScroll();
            })

    },
    components:{
      shopCart,
      cartcontrol,
      food
    }

}
</script>

<style scoped>
	.merchan{
		display: flex;
		position: absolute;
		bottom: 46px;
		top: 174px;
		width: 100%;
		overflow: hidden;
	}
	.menu-wrap{
		/*flex:1;*/
		flex:0 0 auto;
		width: 80px;
		background:#eee;	
	}
	.foodShow{
		flex:1;
		/*overflow: scroll;*/
	}
	.menu-wrap .menu-ul{
	}
	.menu-wrap .menu-li{
		display: table;
	    width: 80%;
	    padding: 10% 10%;
	    text-align: center;
	    height: 12%;
	    border-bottom: 1px solid #e9e9e9;
	}
	.menu-wrap .menu-li.current{
		background: lightgreen;
	}
	.menu-wrap .menu-li .text{
		display: inline-block;
		vertical-align: middle;
		padding: 0 0.8rem;		
	}
	.foodShow .food-list .title{
		padding-left: 14px;
		height: 10%;
		line-height: 200%;
		border-left: 2px solid #d9dde1;
		font-size: 0.2rem;
		color: #93999f;
		background: #f3f5f7;
	}
	.foodShow .food-list .food-item {
		display: flex;
		margin: 18px;
		padding-bottom: 18px;
		position: relative;
	}
	.foodShow .food-list .food-item .icon {
		flex:0 0 57px;
		margin-right: 10px;
	}
	.foodShow .food-list .food-item .content {
		flex:1;
	}
	.foodShow .food-list .food-item .content .name{
		margin: 2px 0 8px 0;
		font-size: 0.4rem;
		color: #07111b;
	}
	.foodShow .food-list .food-item .content .des{
		margin-bottom: 3%;
    	line-height: 12px;
	}
	.foodShow .food-list .food-item .extra {
		line-height: 10px;
		color: #93999f;
	}
	.foodShow .food-list .food-item .content .price{
		line-height: 24px;
	}
	.foodShow .food-list .food-item .content .price .now{
		margin-right: 8px;
		font-size: 14px;
		color: #f01414;
	}
	.foodShow .food-list .food-item .content .price .old{
    	color: #93999f;
    	text-decoration: line-through;
	}
  .cartControl-wrapper {
    display: inline-block;
    position: absolute;
    right: 0;
    bottom: 1.4rem;
  }
</style>



