<template>
	<div class="ratingselect">
		<div class="rating-type">
			<span class="block positive" @click="select(2)" :class="{'active':selectType === 2}">{{desc.all}}<span class="count">{{ratings.length}}</span></span>
			<span class="block positive" @click="select(0)" :class="{'active':selectType === 0}">{{desc.positive}}<span class="count">{{positives.length}}</span></span>
			<span class="block negative" @click="select(1)" :class="{'active':selectType === 1}">{{desc.negative}}<span class="count">{{negatives.length}}</span></span>
		</div>
		<div class="switch" @click="toggleContent()">
			<i class="fa fa-check-circle" aria-hidden="true" :class="{'on':onlycontent}"></i>
			<span class="text">只看有内容的评价</span>
		</div>
	</div>
</template>

<script type="text/javascript">
const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;
	export default{
		props:{
			desc:{
				type:Object,
				default(){
					return{
						all: '全部',
						positive: '满意',
						negative: '吐槽'
					}		
				}
			},
			ratings:{
				type:Array,
				default(){
					return [];
				}
			},
			selectType:{
				type:Number,
				default:ALL
			},
			onlycontent:{
				type:Boolean,
				default:false
			}
		},
		computed:{
			positives(){
				return this.ratings.filter((rating)=>{
					return rating.rateType === POSITIVE;
				})
			},
			negatives(){
				return this.ratings.filter((rating)=>{
					return rating.rateType === NEGATIVE;
				})
			}
		},
		methods:{
			select(type){
				this.selectType = type;
				this.$emit('increment','selectType',type);
			},
			toggleContent(){
				this.onlycontent = !this.onlycontent;
				this.$emit('increment','onlycontent',this.onlycontent);
			}
		}
	}
</script>

<style scoped>
	.ratingselect .rating-type{
		padding: 18px 0;
		margin:0 18px;
	}
	.ratingselect .rating-type .block{
		display:inline-block;
		padding:8px 12px;
		margin-right:8px;
		border-radius:1px;
		font-size:12px;
		color:rgb(77, 85, 93);
	}
	.ratingselect .rating-type .block .count{
		font-size :8px;
		margin-left :2px;
		line-height :16px;
	}
	.ratingselect .rating-type .positive{
		background:rgba(0, 160, 220, 0.2);
	}
	.ratingselect .rating-type .positive.active{
		background:rgb(0, 160, 220);
		color: #fff;
	}
	.ratingselect .rating-type .negative{
		background:rgba(77, 85, 93, 0.2);
	}
	.ratingselect .rating-type .negative.active{
		background:rgb(77, 85, 93);
		color: #fff;
	}
	.ratingselect .switch{
		padding:12px 18px;
		font-size:12px;
    	color:rgb(147, 153, 159);
    	line-height: 24px;
	}
	.ratingselect .switch .on{
		color:#00c850;
	}
	.ratingselect .switch .fa-check-circle{
		display :inline-block;
		vertical-align :top;
		font-size :24px;
		margin-right :4px;
	}  
	.ratingselect .switch .text {
	  font-size:12px;
	}

</style>


