<template>
  <div class="cartControl">
    <!-- <transition name="fade"> -->
      <div class="cart-decrease" @click.stop.prevent="decreaseCart()">
        <transition name="inner">
          <i class="fa fa-minus-circle" aria-hidden="true" v-show="food.count>0"></i>
        </transition>
      </div>
    <!-- </transition> -->
    <div class="cart-count" v-show="food.count>0">{{food.count}}</div>

    <div class="cart-add" @click.stop.prevent="addCart($event)">
      <i class="fa fa-plus-circle" aria-hidden="true"></i>
    </div>
  </div>
</template>

<script type="text/javascript">
import Vue from "vue"
  export default{
    data(){
      return{}
    },
    props:{
      food:{
        type:Object
      }
    },
    methods:{
      addCart(event){
        console.log("this.food.count",this.food.count);
        if(!this.food.count){
          Vue.set(this.food,"count",1);
        }else{
          this.food.count++;
        }
        this.$emit('increment', event.target); 
      },
      decreaseCart(){
        this.food.count--;
      }
    }
  }
</script>

<style scope>
  .cart-decrease,.cart-count,.cart-add{
      display: inline-block;
    }
  .cart-decrease,.cart-add{
      padding: 0.4rem 0.6rem 0.6rem;
      font-size: 2rem;
      color: #00a0dc;
    }
  .cart-count{
      vertical-align: top;
      font-size: 15px;
      line-height: 34px;
    }
  /*.fade-enter-active,.fade-leave-active{
    transition: all 0.4s linear;
  }
  .fade-enter, .fade-leave-active{
    opacity: 0;
    transform:translate3d(24px, 0, 0);
  }*/
  .inner-enter-active, .inner-leave-active {
    transition: all 0.4s linear;
    transform: rotate(0);
  }
  .inner-enter, .inner-leave-active {
    opacity: 0;
    transform:rotate(180deg);
  }

</style>


