<template>
	<transition name="fade">
		<div class="food" v-show="showFlag" ref="elfood">
			<div class="food-content">
				<div class="image-header">
					<img :src="foodselected.image" alt="">
					<div class="back" @click="hide()">
						<i class="fa fa-chevron-left" aria-hidden="true"></i>   
					</div>
				</div>
				<div class="content">
					<h1 class="title">{{foodselected.name}}</h1>
					<div class="detail">
						<span class="sell-count">月售{{foodselected.sellCount}}份</span>
						<span class="rating">好评率{{foodselected.rating}}%</span>
					</div>
					<div class="price">
						<span class="now">￥{{foodselected.price}}</span>
						<span class="old" v-show="foodselected.oldPrice">￥{{foodselected.oldPrice}}</span>
					</div>
					<div class="cartControl-wrapper">
	                    <cartcontrol :food="foodselected"></cartcontrol>
	                </div>
	                <transition name="buy">
	                	<div class="buy" @click.stop.prevent="addFirst()" v-show="!foodselected.count||foodselected.count===0">加入购物车</div>
	                </transition>
				</div>
				<div class="zan"></div>
				<div class="info" v-if="foodselected.info">
				  <h1 class="title">商品信息</h1>
				  <p class="text">{{foodselected.info}}</p>
				</div>
				<div class="zan"></div>
				<div class="rating">
					<h1 class="title">商品评价</h1>
					<ratingselect @increment="incrementTotal" :desc="desc" :ratings="foodselected.ratings" :select-type="selectType" :onlycontent="onlycontent"></ratingselect>
					<div class="rating-wrapper">
						<ul v-show="foodselected.ratings || foodselected.ratings.length">
							<li class="rating-item" v-show="needShow(rating.rateType,rating.text)" v-for="rating in foodselected.ratings">
								<div class="user">
								  <span class="name">{{rating.username}}</span>
								  <img width="12" height="12" :src=rating.avatar alt="" class="avatar">
								</div>
								<div class="time">{{rating.rateTime | formatDate}}</div>
								<p class="text">
								  <i class="iconfont"
								     :class="{'fa fa-thumbs-up':rating.rateType === 0,'fa fa-thumbs-down':rating.rateType === 1}"></i>
								  {{rating.text}}
								</p>
							</li>
						</ul>
						<div class="no-rating" v-show="!foodselected.ratings || foodselected.ratings.length === 0"></div>
					</div>
				</div>
			</div>
		</div>
	</transition>
</template>
<script type="text/javascript">
import BScroll from 'better-scroll';
import cartcontrol from '../cartcontrol/cartcontrol';
import ratingselect from '../ratingselect/ratingselect';
import {formatDate} from '../../assets/js/date.js'
import Vue from "vue"
const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;
	export default{
		props:{
			foodselected:{
				type:Object
			}
		},
		data(){
			return{
				showFlag:false,
				desc:{
					all: '全部',
					positive: '推荐',
					negative: '吐槽'
				},
				selectType:ALL,
				onlycontent:true
			}
		},
		methods:{
			show(){
				this.showFlag = true;	
				this.$nextTick(()=>{
					if(!this.scroll){
						this.scroll = new BScroll(this.$refs.elfood,{
							click:true
						})
					}else{
						this.scroll.refresh();
					}
				})			
			},
			hide(){
				this.showFlag = false;
			},
			addFirst(){
				Vue.set(this.foodselected,'count',1)
			},
			incrementTotal(type,data){
				this[type] = data;
				this.$nextTick(()=>{
					this.scroll.refresh();
				})
			},
			needShow(type,text){
				if(this.selectType === ALL){
					return true;
				}
				if(this.onlycontent && !text){
					return false;
				}else{
					return type === this.selectType;
				}
			}
		},
		components:{
			cartcontrol,
			ratingselect
		},
		filters:{
			formatDate(time){
				let date = new Date(time);
				return formatDate(date,'yyyy-MM-dd hh:mm')
			}
		}

	}
</script>

<style scoped>
	.food{		
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 48px;
		background: #fff;
		z-index: 30;
	}
	.food.fade-enter-active,.food.fade-leave-active{
		transition:all 0.2s linear;
		transform:translate3d(0,0,0);
	}
	.food.fade-enter,.food.fade-leave-active{
		transform:translate3d(100%,0,0);
	}

	.food .food-content .image-header img{
		width: 100%;
		height: 100%;
	}
	.food .food-content .image-header .back{
		position: absolute;
	    top: 5px;
	    left: 10px;
	    font-size: 20px;
	    color: #fff;
	    background: rgba(0,0,0,0.2);
	    border-radius: 50%;
	    width: 30px;
	    height: 30px;
	    text-align: center;
	    line-height: 30px;
	}
	.food .content{
		position: relative;
		padding: 18px;
	}
	.food .content .title{
		line-height: 14px;
		margin-bottom: 8px;
		font-size: 14px;
		font-weight: 700;
		color: rgb(7,17,27);
	}
	.food .content .detail{
	    margin-bottom: 18px;
	    height: 10px;
	    line-height: 10px;
	    font-size: 10px;
	    color:rgb(147, 153, 159);	
	}
	.food .content .sell-count{
		margin-right: 12px;
	}
	.food .content .price .now{
		margin-right: 8px;
	    font-size: 14px;
	    color: #f01414;
	}
	.food .content .price .old {
	    font-size: 10px;
	    color: #93999f;
	    /*text-decoration: line-through;*/
	}
	.cartControl-wrapper {
	  display: inline-block;
	  position: absolute;
	  right: 0;
	  bottom: 1.4rem;
	}
	.food .content .cartControl-wrapper{
		position: absolute;
		right: 12px;
		bottom: 12px;
	}
	.food .content .buy{
		position: absolute;
		right: 18px;
		bottom: 18px;
		z-index: 10;
		height: 24px;
		line-height: 24px;
		padding: 0 12px;
		box-sizing: border-box;
		border-radius: 12px;
		font-size: 10px;
		color: #fff;
		background: #00a0dc;
	}
	.buy-enter-active,.buy-leave-active{
		transition:all .5s;
		opacity: 1
	}
	.buy-enter,.buy-leave-active{
		opacity: 0;
	}
	.food .info{
	  	padding:18px;
	}
	.food .info .title{
		line-height:14px;
		margin-bottom:6px;
		font-size:14px;
		color:rgb(7, 17, 27);
	}    
	.food .info .text{
		font-size:12px;
		line-height:24px;
		padding:0 8px;
		color:rgb(77, 85, 93);
	}  
	.food .zan{
		width: 100%;
	    height: 16px;
	    border-top: 1px solid rgba(1,17,27,0.1);
	    border-top: 1px solid rgba(1,17,27,0.1);
	    background: #f3f5f7;
	}  
	.food .rating{
		font-size:10px;
		padding-top:18px;
	}
	.food .rating .title{
		line-height:14px;
	    margin-left:18px;
	    font-size:14px;
	    color:rgb(7, 17, 27);
	}

		.food .rating .rating-wrapper{
			padding:0 18px;
		}
		.food .rating .rating-wrapper .rating-item{
			position :relative;
	        padding :16px 0;
	       
		}
		.food .rating .rating-wrapper .user{
			position :absolute;
	        right :0;
	        top :16px;
	        font-size :0;
	        line-height :12px;
		}
		.food .rating .rating-wrapper .user .name{
			display :inline-block;
			vertical-align: top;
			font-size: 10px;
			color: rgb(147, 153, 159);
			margin-right :6px;
		}
		.food .rating .rating-wrapper .user .avatar{
			border-radius: 50%;
		}
		.food .rating .rating-wrapper .time{
			margin-bottom :6px;
			line-height: 12px;
			font-size: 10px;
			color :rgb(147, 153, 159);
		}
		.food .rating .rating-wrapper .text{
			line-height: 16px;
			font-size :12px;
			color: rgb(7, 17, 27);
		}
		.food .rating .rating-wrapper .text .iconfont{
			margin-right :4px;
			line-height: 16px;
			font-size: 12px;
		}
		.food .rating .rating-wrapper .text .fa-thumbs-up{
			color :rgb(0, 160, 220);
		}
		.food .rating .rating-wrapper .text .fa-thumbs-down{
			color :rgb(147, 153, 159);
		}
		.food .rating .rating-wrapper .no-rating{
			padding :16px 0;
			font-size :12px;
			color :rgb(147, 153, 159);
		}
</style>


